.App {
    text-align: center;
    width: 350px;
    height: 600px;
    justify-content: flex-start;
    align-items: center;
    display: flex;
    flex-direction: column;

}

.frontPageLogo {
    width: 80%;
}

.frontPageButton {
    width: 50%;
    margin-top: 20px;
}

.frontPageBottom {
    align-items: flex-end;
    display: flex;
    height: 100%;
    justify-content: center;
    width: 100%;
    margin-bottom: 15px;
    gap: 5px;
}

a:visited{
    color: blue;
}

header{
    width: 100%;
    height: 80px;
    background-color: aliceblue;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 30px;
}

.headerLogo {
    height: 50px;
    margin-left: 20px;
}

.content {
    text-align: center;
    width: 350px;
    height: 520px;
    overflow-y: auto;
    justify-content: flex-start;
    align-items: center;
    display: flex;
    flex-direction: column;
    position: relative;
}

.mnemonic {
    color: #faad14;
    background-color: aliceblue;
    border: 1px solid #ffe58f;
    padding: 5px;
    text-align: justify;
    width: 310px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 10px;
}

.seedPhraseContainer {
    width: 310px;
    min-height: 160px;
    padding: 5px;
    margin-top: 30px;
}

.walletName{
    font-weight: bold;
    margin-bottom: 5px;
}

.logoutButton {
    width: 30px;
    height: 30px;
    justify-content: center;
    align-items: center;
    display: flex;
    position: absolute;
    right: 20px;
    top: 5px;
    border-radius: 100px;
}

.logoutButton:hover{
    background-color: rgba(0x 0x 0x 0.036);
    cursor: pointer;
}

.walletView {
    width: 350px;
    padding: 10px;
    height: 100%;
    overflow-y: auto;
}

.sendRow {
    margin-bottom: 10px;
    display: flex;
    justify-content: flex-start;
    gap: 10px;
    align-items: center;
}

.nftImage {
    margin-bottom: 10px;
    height: 240px;
    width: 240px;
    object-fit: cover;
    border-radius: 10px;
}